<template>
    <div id="confianca" class="wrapper">

        <div class="static-container">
            <h1 class="title">
                Galery<br v-if="viewport.is568">(<span class="params">'Story'</span>)
            </h1>

            <div class="std">
                <p class="-purple -full" v-if="viewport.is568">
                    // Mencoba cerita tentang sekitar<br>
                </p>
                <p class="-purple -full" v-else>
                    // Mencoba cerita tentang sekitar<br>
                </p>
                <p>silahkan klik dibawah ini </p>
                <a href="https://drive.google.com/drive/folders/117kARz0R4zBrh7YjbuMP8L50SUrJ96dg?usp=sharing" title="Download PDF" target="_blank" class="bt">
                    dibaca-baca <svg class="nav-ico -blank" viewBox="0 0 16 16"><path d="M5,1h10v10 M5.5,10.5l0.8-0.8"/></svg>
                </a>

                <h2 class="subtitle">
                    Karna tidak ada yang bisa diajak cerita,<br>
                    jadi saya mencoba bercerita lewat tulisan
                </h2>
                <p class="-gray">
                    Mohon maaf sebelumnya, hanya sekedar bercerita saja, <br>
                    tidak ada unsur apapun, mencoba mengeluh pada tulisan
                </p>
            </div>

            

            <nav class="work-nav">
                <h3>// More...</h3>
                <ul>
                    <li class="prev">
                        <router-link to="/work/foto" title="Foto">
                            <svg class="nav-ico -prev" viewBox="0 0 16 16"><path d="M5,1h10v10 M5.5,10.5l0.8-0.8"/></svg>
                            Foto...
                        </router-link>
                    </li>
                    <li class="next">
                        <router-link to="/work/karya" title="Karya">
                            ...Karya
                            <svg class="nav-ico -next" viewBox="0 0 16 16"><path d="M5,1h10v10 M5.5,10.5l0.8-0.8"/></svg>
                        </router-link>
                    </li>
                </ul>
            </nav>

            

        </div>

    </div>
</template>

<script>
    // styles
    import '@/styles/work.scss'
    import '@/styles/work/confianca.scss'
    // ScrollMagic
    import * as ScrollMagic from "scrollmagic";

    export default {
        name: 'confianca',
        props: {
            viewport: Object,
        },
        data() {
            return {
                intro: new TimelineMax(),
                scroller: new ScrollMagic.Controller(),
                qty: 0,
            }
        },
        methods: {
            addQty(){
                if (this.qty === 10) return false; // limit
                return this.qty++
            },
            removeQty(){
                if(this.qty <= 1) return this.qty = 0;
                return this.qty--;
            },
        },
        mounted() {

            /**
             * @desc
             * Intro scene
             */
            this.intro
                .clear(true)
                .addLabel('enter', 1)
                .from('.title', 2, {
                    autoAlpha: 0,
                    rotationX: 20,
                    transformOrigin: '50% 50% -100px',
                    ease: Power3.easeOut,
                }, 'enter')
                .from('.std', 2, {
                    autoAlpha: 0,
                    x: -32,
                    ease: Power3.easeOut,
                }, 'enter+=1.5');

            /**
             * @desc
             * bg color scene
             */
            let sceneBg = new ScrollMagic.Scene({
                triggerElement: '.project-start',
                offset: this.viewport.h/4,
                duration: document.body.offsetHeight
            })
                .addTo(this.scroller)
                .reverse(true);
            sceneBg
                .on('enter', (e) => {
                    document.body.classList.add('-confianca-bg');
                })
                .on('leave', (e) => {
                    if(e.scrollDirection === 'REVERSE') {
                        document.body.classList.remove('-confianca-bg');
                    }
                });

            /**
             * @desc
             * mobile screens scene
             */
            let tlMobile = new TimelineMax();

            tlMobile
                .staggerFrom('.screen', 4, {
                    autoAlpha: 0,
                    yPercent: 25,
                    stagger: .5,
                    ease: Power3.easeOut,
                });

            let sceneMobile = new ScrollMagic.Scene({
                triggerElement: '.project-start',
                offset: -this.viewport.h / 4,
                duration: this.viewport.h
            })
                .setTween(tlMobile)
                .addTo(this.scroller)
                .reverse(true)
                .setClassToggle('.mobile-screens', "active");

            /**
             * @desc
             * scroll screens scene
             */
            let tlScrolls = new TimelineMax();

            tlScrolls
                .to('.scroll-pages img', 4, {
                    top: '-100%',
                })
                .to('.scroll-pages .mobile', 4, {
                    yPercent: 20,
                }, 0);

            let sceneScrolls = new ScrollMagic.Scene({
                triggerElement: '.scroll-pages',
                duration: this.viewport.h
            })
                .setTween(tlScrolls)
                .addTo(this.scroller)
                .reverse(true)
                .setClassToggle('.scroll-pages', "active");

            /**
             * @desc
             * end screens scene
             */
            let tlEnd = new TimelineMax(),
                end = document.querySelector('.end-screens').offsetHeight;

            tlEnd
                .staggerFrom('.end-screens .screen', 4, {
                    xPercent: -50,
                    ease: Power3.easeOut,
                    stagger: .2
                });

            let sceneEnd = new ScrollMagic.Scene({
                triggerElement: '.end-screens',
                offset: -end,
                duration: end
            })
                .setTween(tlEnd)
                .addTo(this.scroller)
                .reverse(true)
                .setClassToggle('.end-screens', "active");

        },
        beforeDestroy() {

            this.scroller.destroy();

        }
    };
</script>
